import { EllipsisTooltip, TranslatedInfo } from "@rp/react/web/common/composite-components"
import { DocumentDate } from "../DocumentDate"

const Divider = ({ dense }: { dense?: boolean }) => (
  <strong
    style={{
      marginLeft: dense ? 0 : 8,
      marginRight: dense ? 2 : 10,
      marginBottom: 2,
      fontSize: 30,
      lineHeight: dense ? "0px" : "15px",
    }}
  >
    Â·
  </strong>
)

const DateAndTranslatedInfo = ({
  language,
  timestamp,
  dense,
}: {
  language?: string | undefined
  timestamp?: string | undefined
  dense?: boolean
}) => (
  <>
    {timestamp && (
      <>
        <Divider dense={dense} />
        <DocumentDate timestamp={timestamp} />
      </>
    )}
    {language && (
      <>
        <Divider dense={dense} />
        {language !== "English" ? <TranslatedInfo language={language} /> : null}
        <EllipsisTooltip typographyProps={{ fontSize: 12 }} tooltipText={language ?? ""}>
          <span data-testid="document-language">{language}</span>
        </EllipsisTooltip>
      </>
    )}
  </>
)

export { DateAndTranslatedInfo }
